import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-input-form-example',
  templateUrl: './input-form-example.component.html',
  styleUrls: ['./input-form-example.component.css']
})
export class InputFormExampleComponent implements OnInit {
  date: Date = new Date();
  name = 'hello';
  fruits: Array<string> = [];
  constructor() { }

  ngOnInit() {
    this.fruits = [ 'apple', 'orange', 'banana' ];
  }

}



