import {Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'sexReform'})
export class SexReform implements PipeTransform{
  transform(value: string, type?: number) {
    switch (value) {
      case '1': return '男' + type;
      case '0': return '女' + type;
      default: return '不明' + type;
    }
  }


}



