import { BrowserModule } from '@angular/platform-browser';
import {ModuleWithProviders, NgModule} from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {
  MdButtonModule, MdCheckboxModule, MdInputModule,
  MdTabsModule, MdSelectModule, MdCardModule, MdMenuModule,
  MdIconModule
} from '@angular/material';
import 'hammerjs';

import { AppComponent } from './app.component';
import { InputFormExampleComponent } from './input-form-example/input-form-example.component';
import {SexReform} from './input-form-example/SexReform';
import {TextFilterPipe} from './input-form-example/filter.pipe';
import { HeaderComponent } from './header/header.component';
import { ParentComponent } from './parent/parent.component';
import { ChildComponent } from './parent/child/child.component';
import {RouterModule} from '@angular/router';
import {rootRouterConfig} from './app.routes';
import { CompComponent } from './comp/comp.component';
import { MyformComponent } from './myform/myform.component';

const rooRouterModule: ModuleWithProviders = RouterModule.forRoot(rootRouterConfig);

@NgModule({
  declarations: [
    AppComponent,
    InputFormExampleComponent,
    SexReform,
    TextFilterPipe,
    HeaderComponent,
    ParentComponent,
    ChildComponent,
    CompComponent,
    MyformComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    BrowserAnimationsModule,
    MdButtonModule, MdCheckboxModule, MdInputModule, MdTabsModule,
    MdSelectModule, MdCardModule, MdMenuModule, MdIconModule,
    rooRouterModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



