import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Child} from './child.model';

@Component({
  selector: 'app-child',
  templateUrl: './child.component.html',
  styleUrls: ['./child.component.css']
})
export class ChildComponent implements OnInit {
  _childValue: Child;

  @Input()
  set childValueObj(childValue: Child) {
    this._childValue = childValue;
    if (this._childValue.sex) {
      if (this._childValue.sex === '1') {
        this._childValue.sex = 'male';
      } else {
        this._childValue.sex = 'female';
      }
    } else {
      this._childValue.sex = '不明';
    }
  }

  get childValueObj() {
    return this._childValue;
  }

  @Output() routerNavigate = new EventEmitter<string>();

  constructor() {
  }

  ngOnInit() {
  }

  btnClick() {
    this.routerNavigate.emit();
  }
}



