import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  links: Array<object>;

  ngOnInit(): void {
    this.links = this.getLinks();
  }

  getLinks() {
    const links = [{
      link: '/parent',
      label: 'component1'
    }, {
      link: '/comp/custom-value',
      label: 'component2'
    }, {
      link: '/myform',
      label: 'myform'
    }];
    return links;
  }
}



